import React, { useRef, useState, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import AlloyFinger from 'alloyfinger'; // 
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const PdfViewer = ({ filePath }) => {

        const containerRef = useRef(null);
        const scaleRef = useRef(1); //  useRef  scale
        const positionRef = useRef({ x: 0, y: 0 }); // 
        const [numPages, setNumPages] = useState(null); // PDF
        const [pageNumber, setPageNumber] = useState(1); // 

        // AlloyFinger
        useEffect(() => {
            const container = containerRef.current;

            const alloyFinger = new AlloyFinger(container, {
                pinch: (evt) => {
                    // 
                    window.requestAnimationFrame(() => {
                        const newScale = Math.max(0.5, Math.min(scaleRef.current * evt.zoom, 3));
                        scaleRef.current = newScale; //  ref
                        container.style.transform = `translate(${positionRef.current.x}px, ${positionRef.current.y}px) scale(${newScale})`;
                    });
                },
                pressMove: (evt) => {
                    // 
                    const maxTranslateX = (container.scrollWidth * scaleRef.current) - container.clientWidth;
                    const maxTranslateY = (container.scrollHeight * scaleRef.current) - container.clientHeight;

                    positionRef.current.x += evt.x;
                    positionRef.current.y += evt.y;

                    positionRef.current.x = Math.min(Math.max(positionRef.current.x, -maxTranslateX), 0);
                    positionRef.current.y = Math.min(Math.max(positionRef.current.y, -maxTranslateY), 0);

                    // 
                    window.requestAnimationFrame(() => {
                        container.style.transform = `translate(${positionRef.current.x}px, ${positionRef.current.y}px) scale(${scaleRef.current})`;
                    });
                }
            });

            return () => {
                alloyFinger.destroy(); // 
            };
        }, []); // 

        const onLoadSuccess = ({ numPages }) => {
            setNumPages(numPages); // 
        };

        return (
            <div ref={containerRef} style={{
                width: '100%',
                minHeight: '100%',
                position: 'relative',
                backgroundColor: '#f0f0f0',
                // transformOrigin: 'top left',
            }}>
                <Document
                    file={filePath}
                    onLoadSuccess={onLoadSuccess}
                    renderMode="svg" // SVG
                >
                    <Page
                        pageNumber={3}
                        scale={scaleRef.current} //  ref 
                    />
                </Document>
            </div>
        );
    };

    export default PdfViewer;
